// Abstract
.abstract {
  &-content {
    padding: rem(120) 0;
    .dropdown.dropdown-nav {
      display: none; } }
  &-side {
    ul {
      max-width: rem(300);
      padding: 0;
      list-style: none;
      li {
        &:not(:last-child) {
          margin-bottom: rem(10); }
        a {
          display: block;
          padding: rem(20) rem(35);
          background: #EEEEEE;
          color: #333333;
          border-radius: 20px;
          transition: all .3s ease-in-out;
          &:hover {
            text-decoration: none;
            background: darken(#EEEEEE, 5%); } } } } }
  &-categories {
    &-desc {
      padding: rem(120) 0 rem(60);
      background: #FFF; }
    &-content {
      padding: rem(60) 0 rem(120);
      background: #EEEEEE;
      .box-categories {
        margin-bottom: rem(24);
        .box-title {
          padding: rem(20) rem(50);
          background: #3B4676;
          border-radius: 30px 30px 0 0;
          h3 {
            margin-bottom: 0;
            color: #FFF; } }

        .box-content {
          padding: rem(30) rem(40);
          background: #FFF;
          border-radius: 0 0 30px 30px;
          .custom-list {
            column-count: 2;
            li {
              margin-bottom: rem(8); } } } } } }
  @include mobile {
    &-side {
      ul {
        display: none; } }
    &-content {
      .dropdown.dropdown-nav {
        margin-bottom: rem(32); } }
    &-categories {
      &-content {
        .box-categories {
          .box-title {
            padding: rem(20) rem(24); }
          .box-content {
            padding: rem(20) rem(24);
            .custom-list {
              column-count: 1; } } } } } } }

.abstract-travel {
