.programme-atglance {
  padding: 120px 0;
  .section-heading {
    margin-bottom: 30px;
    h2 {
      font-size: 24px;
      line-height: 148%;
      @include font-500;
      margin-bottom: 20px; }
    p {
      font-size: 16px;
      line-height: 148%; } }
  .rundown-event {
    margin-bottom: 50px;
    table {
      width: 100%;
      font-size: 18px;
      line-height: 27px;
      th,
      td {
        width: calc(100% / 6); //16.666%
        padding: 40px;
        color: #FFF; }
      th {
        &:first-child {
          border-radius: 20px 0 0 0; }
        &:last-child {
          border-radius: 0 20px 0 0; } }
      tr {
        &:last-child {
          td {
            &:first-child {
              border-radius: 0 0 0 20px; } } }
        &:first-child {
          td:last-child()[rowspan="2"] {
            border-radius: 0 0 20px 0; } } } } }

  .box-seemore {
    padding: 50px;
    background: #FFF;
    border-radius: 30px;
    box-shadow: 0px 10px 50px 0px rgba(0, 0, 0, 0.10);
    display: flex;
    align-items: center;
    gap: 100px;
    h3 {
      max-width: 300px;
      margin-bottom: 0; } }

  @include maxtablet {
    .rundown-event {
      width: 100%;
      overflow: hidden;
      overflow-x: auto;
      table {
        overflow: auto; } } }
  @include mobile {
    .box-seemore {
      display: block;
      padding: 40px;
      h3 {
        line-height: 1.5em;
        margin-bottom: 20px; } } } }

// Login
.login {
  min-height: 100vh;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  &-wrap {
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center; }
  .box-login {
    display: block;
    padding: rem(60);
    background: #FFF;
    border-radius: 30px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    transition: all .3s ease-in-out;
    box-shadow:  0px 10px 50px rgba(0, 0, 0, 0.1);
    &:hover {
      text-decoration: none;
      box-shadow:  0px 10px 50px rgba(0, 0, 0, 0.4); }
    .icon {
      height: rem(82);
      margin: 0 auto;
      margin-bottom: rem(30);
      img {
        width: auto;
        height: rem(82); } }
    .title {
